import React from 'react';
import '../resource/components/Stargazing.sass';

const defaultStyle = {
  '--z-index': '-1'
};
const defaultMoonStyle = {
  '--z-index': '-1'
};

function StargazingBackground(props) {
  const { starStyle = {}, moonStyle = {}, className, children } = props;
  const attrib = { ...props };
  delete attrib.starStyle;
  delete attrib.moonStyle;
  delete attrib.className;
  delete attrib.children;
  return (
    <div className={`parent-background ${className}`} {...attrib}>
      <div className="stars" style={{ ...defaultStyle, ...starStyle }} />
      <div className="stars2" style={{ ...defaultStyle, ...starStyle }} />
      <div className="stars3" style={{ ...defaultStyle, ...starStyle }} />
      <div className="moon" style={{ ...defaultMoonStyle, ...moonStyle }} />
      {children}
    </div>
  );
}

export default StargazingBackground;
