import React, { Component } from 'react';
import { Navbar, Nav, NavbarBrand } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import '../resource/components/Navigation.css';

const BrandNavigation = ({ item, handler }) => (
  <LinkContainer to={item.link}>
    <NavbarBrand onClick={handler}>{item.component}</NavbarBrand>
  </LinkContainer>
);

class Navigation extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isBrandClicked: true
    };
  }

  createNavItems(navigationItems, isBrandClicked) {
    const mappedNavItems = navigationItems.map(item => (
      <React.Fragment key={`${item.key}-fragment`}>
        <LinkContainer
          to={item.link}
          key={item.key}
          {...(isBrandClicked ? { active: false } : {})}
        >
          <Nav.Link
            className="navigation_menu"
            onClick={() => this.setState({ isBrandClicked: false })}
          >
            {item.component}
          </Nav.Link>
        </LinkContainer>
        <div key={`${item.key}-divider`} className="navigation_divider" />
      </React.Fragment>
    ));
    return mappedNavItems;
  }

  createNav(brand, navigationItems, isBrandClicked) {
    return (
      <Navbar bg="dark" variant="dark" className="first-layer">
        {brand != null && (
          <BrandNavigation
            item={brand}
            handler={() => this.setState({ isBrandClicked: true })}
          />
        )}
        <Nav className="mr-auto">
          {this.createNavItems(navigationItems, isBrandClicked)}
        </Nav>
      </Navbar>
    );
  }

  render() {
    const { brand, navigationItems } = this.props;
    const { isBrandClicked } = this.state;
    return this.createNav(brand, navigationItems, isBrandClicked);
  }
}

export default Navigation;
