import React, { Component } from 'react';
import '../resource/pages/PlaceHolder.css';
import Logo from '../resource/marl.png';
import StargazingBackground from '../components/StargazingBackground';

const loading = `Error 404: Page Not Found`;
const content = `This is not the page you are looking for.`;

function createComponent() {
  return (
    <StargazingBackground className="work-in-progress">
      <img src={Logo} alt="Home Logo" width="400" height="400" />
      <div className="alert alert-danger error" role="alert">
        <a href="/" className="alert-link">
          {loading}
        </a>
        <hr />
        <div>{content}</div>
      </div>
    </StargazingBackground>
  );
}

class PlaceHolder extends Component {
  render() {
    return createComponent();
  }
}

export default PlaceHolder;
