import React, { Component } from 'react';
import './resource/Main.css';
import Navigation from './components/Navigation';
import Routes from './Routes';
import Logo from './resource/marl.png';

const BrandLogo = () => (
  <React.Fragment>
    <img
      src={Logo}
      alt="Icon"
      width="40"
      height="30"
      className="d-inline-block align-top"
    />
  </React.Fragment>
);

const BrandItem = {
  link: '/',
  component: BrandLogo()
};

const NavigationItems = [
  {
    link: '/About',
    component: 'About Me',
    key: 'AboutMe'
  },
  {
    link: '/PaperWorks',
    component: 'Paper Works',
    key: 'Paper Works'
  }
];

function createComponent() {
  return (
    <React.Fragment>
      <Navigation brand={BrandItem} navigationItems={NavigationItems} />
      <div className="main">
        <Routes />
      </div>
    </React.Fragment>
  );
}

class App extends Component {
  render() {
    return createComponent();
  }
}

export default App;
