import React from 'react';
import sanitizeHtml from 'sanitize-html';

const defaultOption = {
  allowedTags: ['b', 'i', 'em', 'strong', 'p', 'ul', 'li', 'ol', 'a'],
  allowedAttributes: {
    a: ['href']
  },
  allowedIframeHostnames: ['www.youtube.com']
};

export const sanitize = (dirty, options) => ({
  __html: sanitizeHtml(dirty, { ...defaultOption, ...options })
});

export function SanitizeHtml(props) {
  const { html, options } = props;
  const attrib = { ...props, ...{ html: null, options: null } };
  return (
    // eslint-disable-next-line react/no-danger
    <div {...attrib} dangerouslySetInnerHTML={sanitize(html, options)} />
  );
}

export default { sanitize, SanitizeHtml };
