import React, { Component } from 'react';
import 'react-image-lightbox/style.css';
import Lightbox from 'react-image-lightbox';
import '../resource/components/ProfilePicture.css';

class ProfilePicture extends Component {
  constructor(props) {
    super(props);

    this.toggleOpen = this.toggleOpen.bind(this);
    this.state = {
      photoIndex: 0,
      isOpen: false
    };
  }

  toggleOpen() {
    const { isOpen } = this.state;
    this.setState({ isOpen: !isOpen });
  }

  profilePicture(images, photoIndex, isOpen) {
    return (
      <div className="profile-picture">
        <button type="button" onClick={this.toggleOpen}>
          <img src={images[0]} alt="Profile" className="profile" />
        </button>
        {isOpen && (
          <Lightbox
            mainSrc={images[photoIndex]}
            nextSrc={images[(photoIndex + 1) % images.length]}
            prevSrc={images[(photoIndex + images.length - 1) % images.length]}
            onCloseRequest={this.toggleOpen}
            onMoveNextRequest={() =>
              this.setState({
                photoIndex: (photoIndex + 1) % images.length
              })
            }
            onMovePrevRequest={() =>
              this.setState({
                photoIndex: (photoIndex + images.length - 1) % images.length
              })
            }
          />
        )}
      </div>
    );
  }

  render() {
    const { photoIndex, isOpen } = this.state;
    const { images } = this.props;
    return this.profilePicture(images, photoIndex, isOpen);
  }
}

export default ProfilePicture;
