import React, { Component } from 'react';
import { Nav } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import { Route, Switch } from 'react-router-dom';
import '../resource/pages/AboutAuthor.css';
import Image1 from '../resource/profile/1.jpg';
import Image2 from '../resource/profile/2.jpg';
import ProfilePicture from '../components/ProfilePicture';
import AboutWork from '../components/AboutWork';
import AboutPersonal from '../components/AboutPersonal';
import { name, description } from '../data/Work.json';

const images = [Image1, Image2];

function createComponent() {
  return (
    <div>
      <div className="profile-header header">
        <ProfilePicture images={images} />
        <h4>{name}</h4>
        <p>{description}</p>
        <Nav className="mr-auto d-none">
          <LinkContainer to="/About/Work">
            <Nav.Link className="navigation_menu">Work</Nav.Link>
          </LinkContainer>
          <LinkContainer to="/About/Personal">
            <Nav.Link className="navigation_menu">Personal</Nav.Link>
          </LinkContainer>
        </Nav>
      </div>
      <div className="profile-body">
        <Switch>
          <Route path="/About/Work" component={AboutWork} />
          <Route path="/About/Personal" component={AboutPersonal} />
          <Route path="/About*" component={AboutWork} />
        </Switch>
      </div>
    </div>
  );
}

class AboutAuthor extends Component {
  render() {
    return createComponent();
  }
}

export default AboutAuthor;
